<template lang="html">
  <div class="homenav">
    <!-- h0x -->
    <div class="h0x" v-if="this.teamFlag == 0">
      <div class="row">
        <div class="col-xs-6" ref="topInfo" @click="headToggleFunc(0,'h0x')" :class="{color333:!this.h0xToggle}">
          <div class="h0xItem" :class="{'left133':h0xToggle == 1}"><span></span></div>
        </div>
        <div class="col-xs-6" @click="headToggleFunc(1,'h0x')" :class="{color333:this.h0xToggle}"></div>
      </div>
    </div>

    <!-- h1x -->
    <div class="h1x" v-if="this.teamFlag == 1">
      <div class=""></div>
      <router-link :to="{ name: 'trainHot', params: {} }">
        <span class="fa fa-pencil posaRight"></span><!--  -->
      </router-link>
      <span class="fa fa-chevron-left posaLeft" v-show="!$store.state.nshow" @click="backTeamFunc(1)"></span><!--  -->
      <div class="col-xs-6" ref="topInfo"
        @click="headToggleFunc(0,'h1x')"
        :class="{color333:!this.h1xToggle}">
        
        <div class="h0xItem" :class="{'left133':h1xToggle == 1}"><span></span></div>
      </div>
      <div class="col-xs-6" @click="headToggleFunc(1,'h1x')" :class="{color333:h1xToggle}"></div>

    </div>

    <!-- h11x  -->
    <div class="h0x" v-if="this.teamFlag == 11">
      <div class="fff"></div>
      <span class="fa fa-chevron-left posaLeft posa" @click="backFunc(1)"></span>
    </div>

    <!-- h12x -->
    <div class="h0x" v-if="this.teamFlag == 12">
      <div class="fff"></div>
      <span class="fa fa-chevron-left posaLeft posa" @click="backFunc(11)"></span>
      <i class="fa fa-paper-plane posa posaRight"></i>
    </div>

    <!-- h2x -->
    <div class="h1x" v-if="this.teamFlag == 2">
      <div class=""></div>
      <router-link :to="{ name: 'trainHot', params: {} }">
        <span class="fa fa-pencil posaRight"></span><!--  -->
      </router-link>
      <span class="fa fa-chevron-left posaLeft" v-show="!$store.state.nshow" @click="backTeamFunc(1)"></span><!--  -->
      <div class="col-xs-6" ref="topInfo"
        @click="headToggleFunc(0,'h2x')"
        :class="{color333:!this.h2xToggle}">
        
        <div class="h0xItem" :class="{'left133':h2xToggle == 1}"><span></span></div>
      </div>
      <div class="col-xs-6" @click="headToggleFunc(1,'h2x')" :class="{color333:h2xToggle}"></div>

    </div>

    <!-- h3x   -->
    <div class="h1x" v-if="this.teamFlag == 3">
      <div class=""></div>
      <router-link :to="{ name: 'trainHot', params: {} }">
        <span class="fa fa-pencil posaRight"></span><!--  -->
      </router-link>
      <span class="fa fa-chevron-left posaLeft" v-show="!$store.state.nshow" @click="backTeamFunc(1)"></span><!--  -->
      <div class="col-xs-4" ref="topInfo"
        @click="headToggleFunc(0,'h3x')"
        :class="{color333:h3xToggle == 0}">
        
        <div class="h0xItem" :class="{'left133':h3xToggle == 1,'left233':h3xToggle == 2}"><span></span></div>
      </div>
      <div class="col-xs-4" @click="headToggleFunc(1,'h3x')" :class="{color333:h3xToggle == 1}"></div>
      <div class="col-xs-4" @click="headToggleFunc(2,'h3x')" :class="{color333:h3xToggle == 2}"></div>
      <div class="posa w100">
      </div>
    </div>

    <!-- h4x  -->
    <div class="h1x" v-if="this.teamFlag == 4">
      <div class=""></div>
      <router-link :to="{ name: 'trainHot', params: {} }">
        <span class="fa fa-pencil posaRight"></span><!--  -->
      </router-link>
      <span class="fa fa-chevron-left posaLeft" v-show="!$store.state.nshow" @click="backTeamFunc(0)"></span><!--  -->
    </div>

    <!-- h5x  -->
    <div class="h1x" v-if="this.teamFlag == 5">
      <div class=""></div>
      <span class="fa fa-chevron-left posaLeft" v-show="!$store.state.nshow" @click="backTeamFunc(0)"></span><!--  -->
    </div>

    <!-- h6x  -->
    <div class="h1x blackColor" v-if="this.teamFlag == 6">
      <div class="blackColor">{{ teamName }} </div>
      <span class="fa fa-chevron-left posaLeft "
            v-show="!$store.state.nshow"
            @click="backTeamFunc(0)"
            style="color:#1b1b1b"
            ></span><!--  -->
    </div>


    <!--   -->
    <!-- <div class="h0x" v-if="this.teamFlag == 0">
      <div class="row">
        <div class="col-xs-6" ref="topInfo" @click="headToggleFunc(0,'h0c')" :class="{color333:!this.h0xToggle}">
          <div class="h0xItem" :class="{'left133':h0xToggle == 1}"><span></span></div>
        </div>
        <div class="col-xs-6" @click="headToggleFunc(1,'h0c')" :class="{color333:this.h0cToggle}"></div>
      </div>
    </div> -->
  </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
  name: "team",
  data () {
    return {
      boxShow:true,
      teamHead:'../../static/4x.jpg',
      teamName:'',
      mnub:15,
      teamLeader:"",
      teamMap:'',

    }
  },

  components: {
  },

  computed: {
    ...mapState([
      //team
      'teamFlag',
      'h0xToggle',
      'h1xToggle',
      'h2xToggle',
      'h3xToggle',
      'h4xToggle',
      'h5xToggle',

      //macth
      'macthFlag',
      'h0cToggle',
      'h1cToggle',
      'h2cToggle',
      'h3cToggle',
    ])
  },

  methods: {
    ...mapMutations([
      'headToggle',
      'flag',
      'backTeam',
      'back'
    ]),
    headToggleFunc(i,flag) {
      this.headToggle({i,flag})
    },
    flagFunc(i) {
      this.flag({i})
    },
    backTeamFunc(i) {
      this.backTeam({i})
      this.$router.push({name:'team'})
    },
    backFunc(i) {
      this.$router.go(-1)
      this.back({i})
    }
  }
}
</script>


<style lang="css">

</style>



