<template lang="html">
  <div class="trainList container pt5">
    <div class="page">
      <div class="trainStatus clearfix"><!-- status -->
        <span class="status">{{ trainStatus[1] }}</span>
      </div>
      <!--  -->
      <div class="gps">
        <i class="fa fa-map-marker"></i>
        <span>{{ map }}</span>
      </div>
      <!--  -->
      <div class="time mb10">
        <i class="fa fa-globe"></i>
        <span>{{ timer }}</span>
      </div>
      <!--  -->
      <div class="note">
        <p>{{ trainNote }}</p>
        <i class="fa fa-chain-broken"></i>
      </div>
      <!--  -->
      <div class="member">
        <div class="mb20 mt20">
          <i class="fa fa-blind"></i>
          <span v-if="teamFlag == 1"> ( {{ memberCount }} )</span>
          <span v-if="teamFlag == 11"> ( {{ memberCount }} )</span>
        </div>
        <div class="menberHead">
          <img :src="memberHead" alt="">
          <p>{{ memberUesrName }}</p>
          <p class="check" v-show="teamFlag == 11" :class="{'lv':lv == 0}"></p>
        </div>
        <div class="menberHead">
          <img :src="memberHead" alt="">
          <p>{{ memberUesrName }}</p>
          <p class="check" v-show="teamFlag == 11" :class="{'lv':lv == 1}"></p>
        </div>
        <div class="menberHead">
          <img :src="memberHead" alt="">
          <p>{{ memberUesrName }}</p>
          <p class="check" v-show="teamFlag == 11" :class="{'lv':lv == 2}"></p>
        </div>
        <div class="menberHead">
          <img :src="memberHead" alt="">
          <p>{{ memberUesrName }}</p>
          <p class="check" v-show="teamFlag == 11" :class="{'lv':lv == 3}"></p>
        </div>
        <div class="menberHead">
          <img :src="memberHead" alt="">
          <p>{{ memberUesrName }}</p>
          <p class="check" v-show="teamFlag == 11" :class="{'lv':lv == 4}"></p>
        </div>
      </div>
      <!--  -->
      <div class="setBtn tac" @click="pushNextFunc()" v-if="this.teamFlag == 1">
        
      </div>

      <div class="setBtn tac" @click="editFunc()" v-if="this.teamFlag == 11">
        
      </div>
    </div>
  </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
  data() {
    return {
      trainStatus: ['','',''],
      map: '',
      timer: "2000.11.11 9:00",
      trainNote: '()',
      memberCount: 5,
      memberHead:'./static/4x.jpg',
      memberUesrName: ''
    }
  },
  computed: {
    ...mapState([
      'h1xToggle',
      'teamFlag',
      'lv'
    ])
  },
  methods: {
    ...mapMutations([
      'pushNext',
      'pushEdit'
    ]),

    pushNextFunc() {
      this.pushNext();
      this.$router.push({name:'trainDetail'})
    },

    editFunc() {
      this.pushEdit()
      this.$router.push({name:'trainEdit'})
    }
  }
}
</script>

<style lang="css">
</style>



