import {getStore, setStore, removeStore} from '../components/user/utils.js'
export const STORAGE_KEY = 'aki';
import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);
// state
//  modules: {
//     dialog: dialog_store,
//     other: other,//
// }
export default new vuex.Store({
  state:{
    navshow:true,
    show:false,
    loadShow:false,
    msg: "",
    // nshow: 0
    nshow: 0,
    mainNavShow:true,
    // h0x x(team)
    teamFlag:0,
    h0xToggle:0,
    h1xToggle:0,
    h2xToggle:0,
    h3xToggle:0,
    h4xToggle:false,
    h5xToggle:false,
    h5xToggle:false,

    // h0c c(macth)
    macthHeadFlag:0,
    macthFlag:0,
    h0cToggle:0,
    h1cToggle:0,
    h2cToggle:0,
    h3cToggle:0,

    active:true,

    lv:0,
    //


    //
    userwarp:0,
    user:{
      name:'',
      password:'',
      id:'10241',
      sex:'',
      head:'./static/dang.jpg',
      phone:'17421133233',
      background:'./static/13qpv8blvw3xex6sgnvouqqi0.jpg',
      summary:'4.',
    },
    menber:[
      {
        name:'A',
        head:'./static/dang.jpg',
        img:'./static/da.jpg'
      },
      {
        name:'b',
        head:'./static/logo.png',
        img:'./static/qw.gif'
      },
      {
        name:'v',
        head:'./static/13qpv8blvw3xex6sgnvouqqi0.jpg',
        img:'./static/4x.jpg'
      },
    ],
    hotmsg:[{
      name:"default",
      head:'./static/4x.jpg',
      src:['./static/13qpv8blvw3xex6sgnvouqqi0.jpg'],
      msg:'default Copyright (c) 2018 Copyright Holder All Rights Reserved.;'
    }],
    username:'',
    userid:'123456',
    userhead:'./static/4x.jpg',
    p2userhead:'./static/dang.jpg',
  },
  mutations: {
    //
    login(state, {username, password}) {
      state.user.name = username;
      state.user.password = password;
      let storage = getStore(STORAGE_KEY);
      // storage.user = state.user;
      setStore(STORAGE_KEY, storage);
    },
    register(state, {username, password}) {
      state.user.name = username;
    },

    // hotmsg
    info(state,i) {
      state.hotmsg.push(i)
    },

    // //home
    // saveMsg(state) {
    //   setStore('biu',a)
    // },
    //
    // infoed(state) {
    //   let date = getStore('biu')
    //   // state.hotmsg.push(date)
    //   console.log(date);
    // },
    upUserDate(state,{n,s,p}) {
      state.user.name = n
      state.user.sex = s
      state.user.phone = p
    },
    // p1

    // 
    headToggle (state, {i,flag}) {
      if (flag === 'h0x') {
        state.h0xToggle = i
      }
      if (flag === 'h1x') {
        state.h1xToggle = i
      }
      if (flag === 'h2x') {
        state.h2xToggle = i
      }
      if (flag === 'h3x') {
        state.h3xToggle = i
      }
      if (flag === 'h0c') {
        state.h0cToggle = i
      }
    },

    // 
    flag(state,{i}) {
      if (!i){
        state.teamFlag = 0
      }
      state.active = false
      state.teamFlag = i
      state.mainNavShow = false
      if (i === 1) {

      }
      if (i === 2) {

      }
    },

    //team
    backTeam(state, {i}) {
      state.mainNavShow = true;
      state.teamFlag = 0;
    },

    //i
    back(state,{i}) {
      state.teamFlag = i
    },

    pushitvite(state,{i}) {
      state.macthHeadFlag = i
    },

    pushMacth(state,{i}) {
      state.macthHeadFlag = i
    },

    //
    pushNext(state) {
      state.teamFlag = 11;
    },

    //
    pushEdit(state) {
      state.teamFlag = 12
    }
  }

})



